body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  width: auto;
  margin: 0 auto;
}


.book{
    width: 300px;
    text-align: center;
    border: 1px solid rgb(5, 186, 192);
    margin: 10px;
    padding: 10px 20px;
    padding-left: 30px;
}
.book:hover{
    background-color: rgba(207, 245, 250, 0.479);
}
.book h2{
    color: brown;
}
.book button{
    width: 70px;
    height: 40px;
    background-color: rgb(199, 237, 240);
    border: 1px solid rgb(80, 219, 243);
    border-radius: 5px;
    font-size: 16px;
    font-weight: 600;
    margin: 15px 5px;
}
.book p{
    font-size: 18px;
    padding: 10px 0;
}
.book span{
    display: block;
    margin: 10px 0;
    _padding: 20px 0;
    color: darkcyan;
    font-style: italic;
    font-size: 20px;
    font-weight: 600;
}
.book b{
    color: #000;
    font-size: 18px;
}
.book h5{
    font-size: 18px;
}
.book img{
    height: 250px;
    display: block;
    margin: 10px auto;
}
.w100{
    width: 100%;
    height: auto !important;
}
.library{
    display: flex;
    justify-content: center;
    margin: 10px;
}
button{
    width:100px;
    height: 30px;
    background-color: cadetblue;
    color: white;
    margin:10px 0;
    border:0;
    border-radius: 5px;
    font-size: 16px;
    font-weight:600;
    margin-bottom: 20px;
}
button:hover{
    background-color: rgb(172, 241, 241);
    color: red;
    cursor: pointer;
}
